.sidebar {
  flex: 0.35;
  background-color: #fff;
  overflow-y: scroll;
  overflow-x: auto;
}
.sidebar__header {
  display: flex;
  justify-content: space-between;
  border-right: 0.0625rem solid lightgray;
  padding: 0.625rem;
  background-color: #ededed;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}
.sidebar__header .MuiAvatar-img {
  width: 2.5rem;
  height: 2.45rem;
  border-radius: 40rem;
}
.sidebar__headerRight {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  justify-content: space-evenly;
  min-width: 10vw;
}
.sidebar__headerRight svg {
  font-size: 1.12rem;
  color: rgb(92, 91, 91);
}
.sidebar__headerRight .space {
  margin-right: 0.5rem;
}
.sidebar__headerRight .spaceI {
  width: 0.1rem;
  height: 1rem;
  /* margin-right: 0.2rem; */
}

.sidebar__search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 2.5rem;
  padding: 0.625rem;
}
.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: #fff;
  height: 2.187rem;
  border-radius: 0.7rem;
  width: 100%;
}
.sidebar__searchContainer svg {
  width: 1.3rem !important;
  margin-left: 4px;
}
.sidebar__searchContainer input::-webkit-input-placeholder {
  font-size: 0.9rem !important;
  word-wrap: break-word;
  white-space: pre-wrap;
  word-break: break-word;
}

.sidebar__searchContainer input {
  border: 0;
  margin-left: 0.312rem;
  /* After use border 0 when you click then a black border came to remove this we use outline=0 */
  outline: 0;
  width: 100%;
}

.sidebar__Chats {
  display: flex;
  background-color: #fffffff5;
  flex-direction: column;
  /* color: white; */
  gap: 5px;
}
.sidebar__chat {
  display: flex;
  align-items: center;
  border-bottom: 0.0625rem solid #eaeaea;
  padding: 0.625rem;
}
.sidebar__chat:hover {
  cursor: pointer;
  color: white;
  background-color: #000000;
}
.sidebar__chatInfo {
  margin-left: 0.937rem;
}
.sidebar__chatInfo h2 {
  font-size: 1rem;
}
.sidebar__chatInfo p {
  font-size: 0.75rem;
}
.sidebar__chat > h2 {
  font-size: 1.125rem;
  margin-left: 0.625rem;
  cursor: pointer;
}
.sidebar__chat > .MuiAvatar-circular {
  width: 1.875rem;
  height: 1.875rem;
}

.text__color {
  color: rgb(255, 255, 255);
  text-decoration: none;
  background-color: rgb(91, 90, 90);
}

@media (max-width: 700px) {
  .sidebar__searchContainer svg {
    margin-top: 0.3rem;
  }
}
@media (max-width: 420px) {
  .sidebar__searchContainer input::-webkit-input-placeholder {
    font-size: 0.8rem !important;
  }
  .sidebar__searchContainer input::-webkit-input-placeholder {
    font-size: 1rem !important;
    position: relative;
    bottom: 0.01rem;
  }
}
@media (max-width: 300px) {
  .sidebar__searchContainer input::-webkit-input-placeholder {
    font-size: 1rem !important;
    position: relative;
    bottom: 0.2rem;
  }
  .sidebar__header .MuiAvatar-img {
    position: relative;
    right: 0.9rem;
    width: 3.3rem;
    height: 3.2rem;
    border-radius: 7rem;
  }
  .sidebar__headerRight .space {
    position: relative;
    left: 0.9rem;
  }
  .sidebar__headerRight .spaceI {
    position: relative;
    left: 0.5rem;
  }
}
